import React from "react";
import { Switch, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Login from '../component/Login/index';
import Registration from "../component/Registration/index";
import Dashboard from '../component/Dashboard/index';
import Homepage from '../component/Homepage/index';
import Megapod from '../component/Megapod/index';
import Miner from '../component/Miner/index';
import Dataex from '../component/Dataex/index';

const BaseRouter = (...props) => (

  <Switch>
    <Route exact path="/signup/" component={Registration} />
    <Route exact path="/" component={Login} />      
    <Route exact path="/dashboard" component={ Homepage } />
    <Route  path="/dashboard1" component={ Dashboard } />
    <Route  path="/dataexport" component={ Dataex } />
    <Route  path="/megapod" component={ Megapod } />
    <Route  path="/miner" component={ Miner } /> 
</Switch>
);

